.programme{min-height:calc(100vh - 110px);padding:2rem;overflow-x:hidden}@media(max-width: 650px){.programme{min-height:calc(100vh - 75px);padding:1rem}}.programme-content{max-width:1200px;margin:0 auto;display:flex;flex-direction:column;gap:3rem;width:100%}.programme-text{opacity:0;animation:fadeIn 1s ease-in forwards}.programme-text .title{font-size:2.5rem;margin-bottom:1.5rem;font-weight:300}.programme-text .intro{color:#2c9696;font-size:1.1rem;line-height:1.6;margin-bottom:2rem}.programme-text .activities{background:hsla(0,0%,100%,.436);padding:2rem;color:#2c3e50;border-radius:10px;box-shadow:0 4px 6px rgba(0,0,0,.1)}.programme-text .activities h2{color:#2c3e50;margin-bottom:1rem}.programme-text .activities ul{list-style:none;padding:0}.programme-text .activities ul li{margin-bottom:1rem;padding-left:1.5rem;position:relative}.programme-text .activities ul li:before{content:"•";position:absolute;left:0}.programme-text .note{font-style:italic;color:#b290b6;margin-top:2rem;padding:1rem;border-left:3px solid #b290b6}.programme-container{display:flex;justify-content:center;align-items:center;width:100%}.programme-sizing{width:100%;height:auto;object-fit:contain;object-fit:contain;border-radius:5px}@keyframes fadeIn{from{opacity:0;transform:translateY(20px)}to{opacity:1;transform:translateY(0)}}@media(max-width: 768px){.programme-text{width:100%}.programme-text .title{font-size:1.5rem;word-wrap:break-word}.programme-text .intro{color:#2c9696;font-size:1rem;line-height:1.5;margin-bottom:2rem}.programme-text .activities{padding:1.25rem}.programme-text .activities h2{font-size:1.25rem;margin-bottom:1rem;margin-top:.5rem}}